import { useEffect, useState } from 'react';
import { useLocation, useNavigate } from 'react-router';
import { Routes, Route } from 'react-router-dom';

import './styling/App.css';

import ImageView from './ImageView';
import MainPage from './MainPage';
import HelpPage from './HelpPage';
import LoginPage from './LoginPage';
import Admin from './Admin';

function App() {
    const [images, setImages] = useState([]);
    let navigate = useNavigate();
    useEffect(() => {
        fetch("/api/images")
            .then(response => {
                if(response.redirected) {
                    let target = new URL(response.url).pathname;
                    navigate(target)
                    return;
                }
                response.json()
                    .then((data) => {
                        if(data !== undefined){
                            setImages(data);
                        }
                    })
            })
            .catch((err) => {
                console.log(err.message);
            })
    }, [window.location.href]);

    return (
        <>
            <Header />
            <div id="content">
                <ScrollToTop>
                    <Routes>
                        <Route path="/login" element={<LoginPage />} />

                        <Route path="/" element={<MainPage images={ images } />} />

                        <Route path="/images/:image_id?" element={<ImageView images={ images } />} />

                        <Route path="/help" element={<HelpPage />} />

                        <Route path="/admin" element={<Admin />} />
                    </Routes>
                </ScrollToTop>
            </div>
        </>
    );
}

export default App;



function Header() {
    return (
        <div id="header">
            <div className="text-area">
                <h4>Just Married</h4>
                <h1>Daniel  Wyona</h1>
            </div>
        </div>
    )
}



/**
 * If the location changed, since it last time scrolled to top, it will scroll the page to the top, after it got rendered.
 * @param {*} props.children Will be returned -> nothing will happen to them
 * @returns The children as JSX, that were passed in the props
 */
const ScrollToTop = (props) => {
    const location = useLocation();
    useEffect(() => {
        window.scrollTo(0, 0);
    }, [location]);

    return <>{props.children}</>
};
