import { BackButton, Heading } from "./Components";
import "./styling/HelpPage.css";


export default function HelpPage(props){
    return(
        <>
            <BackButton />

            <div id="help-view">
                <Heading title="Kamera" />
                <div className="centered-image">
                    <img src="/Images/help/RC_Control.png" alt="Anleitung der Fernsteuerun" />
                </div>

                <Heading title="Website" />
                <div className="centered-image">
                    <img src="/Images/help/help_home.png" alt="Screenshot der Startseite" />
                </div>
                <div className="centered-image">
                    <img src="/Images/help/help_imageview.png" alt="Screenshot der Bildanzeige" />
                </div>
                <div className="centered-image">
                    <img src="/Images/help/help_personalized.png" alt="Screenshot der Personalisierungssteuerung" />
                </div>

                <Heading title="Datenschutzhinweise" />
                <ul>
                    <li>
                        Alle geschossenen Fotos werden gespeichert und dem Brautpaar bergeben
                        (nachdem sie sehr oberflchlich gefiltert wurden, z. B. leere Fotos werden gelscht).
                    </li>
                    <li>
                        Keine Fotos werden im Internet hochgeladen, solange es nicht ausdrcklich bei
                        spezifischen Fotos von berechtigten Personen erwnscht wird.
                    </li>
                    <li>
                        Diese Website befindet sich NICHT im Internet und ist NICHT ber das Internet erreichbar.
                    </li>
                    <li>
                        Personalisierte und Schwarz/Wei Fotos werden grundstzlich nicht fr das Brautpaar gespeichert.
                        Ausnahmen sind personalisierte Fotos, die durch Knopfdruck ausdrcklich fr das Brautpaar gespeichert werden sollen.
                    </li>
                </ul>
            </div>
        </>
    );
}