import { useParams } from "react-router";
import { HexColorPicker } from "react-colorful";
import { saveAs } from "file-saver";

import './styling/ImageView.css';
import { useEffect, useState } from "react";

import { BackButton } from "./Components";

const EVENT_TITLE = "Hochzeit-MC"

const UPDATE_DELAY_MS = 400;

const DEFAULT_CUSTOMIZATION = {
    text: ["Daniel + Wyona = <3", "", ""],
    font: "Arathevil",
    font_size: 275,
    h_align: "center",
    v_align: "top",
    color: "#e55574"
}

const EDITIONS = {
    default: "Standard",
    grayscale: "Schwarz-Wei",
    personalized: "Personalisiert"
}
const FONTS = ["Helvetica", "Arathevil", "Palace Script"]
const FONT_SIZE_MULTIPLIER = {
    "Helvetica": 1,
    "Arathevil": 1.5,
    "Palace Script": 2
}


function calculateURL(imageId, edition, customization) {
    let url = imageId + "/" + (edition === "default" ? "" : edition);
    if (edition === "personalized") {
        var queryParameters = [];
        for (var property in customization)
            if (customization.hasOwnProperty(property)) {
                if (property === "text") {
                    queryParameters.push(encodeURIComponent(property) + "=" + encodeURIComponent(customization[property].join(customization["layout"] === "split" ? "<split/>" : "")));
                }
                else if (property === "font_size") {
                    queryParameters.push(encodeURIComponent(property) + "=" + encodeURIComponent(customization[property] * FONT_SIZE_MULTIPLIER[customization["font"]]));
                }
                else {
                    queryParameters.push(encodeURIComponent(property) + "=" + encodeURIComponent(customization[property]));
                }
            }
        url += "?" + queryParameters.join("&");
    }
    return url;
}


function ImageView(props) {
    let { image_id } = useParams();
    var [edition, setEdition] = useState("default");
    var [isDirty, setImageDirty] = useState(false);
    var [customization, setCustomization] = useState(DEFAULT_CUSTOMIZATION);
    var [imageURL, setImageURL] = useState(calculateURL(image_id, edition, customization));

    document.title = "Fotobox - Bild #" + image_id;

    useEffect(() => {
        if (!isDirty) return;

        setImageURL(calculateURL(image_id, edition, customization));
        console.log(imageURL);

        setImageDirty(false);
    }, [isDirty]);

    var imageIDIndex = props.images.findIndex((imageID) => imageID === image_id);
    var previousImage = imageIDIndex > 0 ? props.images[imageIDIndex - 1] : undefined;
    var nextImage = imageIDIndex < props.images.length - 1 && imageIDIndex !== -1 ? props.images[imageIDIndex + 1] : undefined;

    const saveFile = () => {
        console.log("/api/images/" + imageURL);
        saveAs("/api/images/" + imageURL, EVENT_TITLE + "_" + image_id + (edition === "default" ? "" : "_" + edition) + ".jpg");
    }
    const printFile = () => {
        if(image_id === undefined) {
            window.alert("Ein unerwarteter Fehler ist aufgetreten.\nBitte versuchen Sie es spter erneut.");
        }
        if(window.confirm("Sind Sie sich sicher, dass sie dieses Bild" + (edition === EDITIONS.default ? " " : edition === EDITIONS.grayscale ? " in [Schwarz-Wei] " : " [personalisiert] ") + "ausdrucken mchten?")) {
            fetch("/api/printer/order", {
                method: "POST",
                body: JSON.stringify({ image_id: image_id }),
                headers: {
                    'Content-Type': "application/json"
                }
            })
                .then(res => {
                    if(res.status === 200) {
                        window.alert("Ihr Druckauftrag wird verarbeitet...");
                    }
                    if(res.status === 409) {
                        window.alert("Ihr Druckauftrag wurde abgelehnt, da der Drucker aktuell eine Pause macht.\nBitte versuchen Sie es spter erneut.");
                    }
                    if(res.status === 429) {
                        window.alert("Ihr Druckauftrag konnte leider nicht verarbeitet werden, da der Drucker bereits beschftigt ist.");
                    }
                    if(res.status === 500) {
                        window.alert("Ihr Druckauftrag ist leider fehlgeschlagen.\nBitte versuchen Sie es ein wenig spter erneut.");
                    }
                    if(res.status === 503) {
                        window.alert("Ihr Druckauftrag konnte leider nicht verarbeitet werden, da das Papier aufgebraucht ist.\nBitte wenden Sie sich an Christian Zahn.");
                    }
                })
        }
    }

    /*
                    <button className="save-button">Fr das Brautpaar speichern</button>
    */

    return (
        <>
            <div className="top-bar">
                <BackButton target="/"/>
                <div className="action-buttons">
                    {
                        EDITIONS[edition] === EDITIONS.personalized &&
                        <button className="action-button" onClick={() => savePersonalizedImage(image_id, imageURL)}>
                            <img src="../Icons/save.svg" className="icon" />
                        </button>
                    }
                    {
                        EDITIONS[edition] === EDITIONS.default &&
                        <button className="action-button" onClick={printFile}>
                            <img src="../Icons/print.svg" className="icon" />
                        </button>
                    }
                </div>
            </div>
            <div id="image-view">
                <Image url={imageURL} previousImage={previousImage} nextImage={nextImage} />

                <button className="main-button" onClick={saveFile}>
                    Herunterladen
                    <img src="../Icons/download.svg" className="icon" />
                </button>

                <div className="edition-selection">
                    {
                        Object.keys(EDITIONS).map(
                            (option) => {
                                if (option === edition) return undefined;

                                return <button key={option} className="edition-button" onClick={() => { setEdition(option); setImageDirty(true) }}>{EDITIONS[option]}</button>
                            }
                        )
                    }

                </div>

                {
                    edition === "personalized" &&
                    <Customizations setCustomization={setCustomization} setImageDirty={setImageDirty} url={imageURL} image_id={image_id} />
                }
            </div>
        </>
    );
}

export default ImageView;



function Image(props) {
    return (
        <div className="image-container">
            <img alt={"Foto der Fotobox. ID: " + props.url} src={"/api/images/" + props.url} className="main-frame" />

            <div className="switch-control">
                {
                    props.previousImage !== undefined &&
                    <a className="switch-button previous" href={"/images/" + props.previousImage} >
                        <svg focusable="false" role="" aria-hidden="true" xmlns="http://www.w3.org/2000/svg" x="0" y="0" version="1.1" viewBox="0 0 24 24">
                            <path d="m10.4 20.7-1.8-.9 4.5-7.8-4.5-7.8 1.8-.9 5 8.7z" />
                        </svg>
                    </a>
                }
                {
                    props.nextImage !== undefined &&
                    <a className="switch-button next" href={"/images/" + props.nextImage} >
                        <svg focusable="false" role="" aria-hidden="true" xmlns="http://www.w3.org/2000/svg" x="0" y="0" version="1.1" viewBox="0 0 24 24">
                            <path d="m10.4 20.7-1.8-.9 4.5-7.8-4.5-7.8 1.8-.9 5 8.7z" />
                        </svg>
                    </a>
                }
            </div>
        </div>
    );
}


function delay(ms) {
    return new Promise(resolve => setTimeout(resolve, ms));
}


var lastConfigChange;
async function initiateUpdateWithDelay(onUpdate) {
    await delay(UPDATE_DELAY_MS);
    if(Date.now() - lastConfigChange >= UPDATE_DELAY_MS) {
        onUpdate();
    }
}

function Customizations(props) {
    var [config, setConfig] = useState(DEFAULT_CUSTOMIZATION);

    const updateConfig = (newConfig) => {
        setConfig(newConfig);
        lastConfigChange = Date.now();
        initiateUpdateWithDelay(() => {
            props.setCustomization(newConfig);
            props.setImageDirty(true);
        });
    };

    let textUpdate = function (v, isSplit, splitIndex) {
        let content = config["text"];
        if (!isSplit) {
            content[0] = v.target.value;
            content[1] = "";
            content[2] = "";
        }
        else {
            content[splitIndex] = v.target.value;
        }
        updateConfig({ ...config, text: content })
    }

    return (
        <div className="customizations">
            <div className="config">
                <label>Text</label>
                {
                    config["layout"] !== "split" &&
                    <textarea className="input" onChange={v => textUpdate(v, false, 0)} value={config["text"].join((config["text"][1].length === 0 && config["text"][2].length === 0 ? "" : " "))} />
                }
                {
                    config["layout"] === "split" &&
                    <div className="input">
                        <p className="text-input">Links</p>
                        <textarea onChange={v => textUpdate(v, true, 0)} value={config["text"][0]} />
                        <p className="text-input">Mitte</p>
                        <textarea onChange={v => textUpdate(v, true, 1)} value={config["text"][1]} />
                        <p className="text-input">Rechts</p>
                        <textarea onChange={v => textUpdate(v, true, 2)} value={config["text"][2]} />
                    </div>
                }
            </div>

            <div className="config">
                <label>Ausrichtung</label>
                <div className="select-layout">
                    <button
                        className={"select-button" + (config["h_align"] === "left" && config["v_align"] === "top" ? " selected" : "")}
                        onClick={() => updateConfig({ ...config, h_align: "left", v_align: "top" })}>
                        <img className="icon" src="/Icons/Layout/top-left-alignment.png" alt="Icon text layout top left" />
                    </button>
                    <button
                        className={"select-button" + (config["h_align"] === "center" && config["v_align"] === "top" ? " selected" : "")}
                        onClick={() => updateConfig({ ...config, h_align: "center", v_align: "top" })}>
                        <img className="icon" src="/Icons/Layout/top-center-alignment.png" alt="Icon text layout top center" />
                    </button>
                    <button
                        className={"select-button" + (config["h_align"] === "right" && config["v_align"] === "top" ? " selected" : "")}
                        onClick={() => updateConfig({ ...config, h_align: "right", v_align: "top" })}>
                        <img className="icon" src="/Icons/Layout/top-right-alignment.png" alt="Icon text layout top right" />
                    </button>
                    <button
                        className={"select-button" + (config["h_align"] === "left" && config["v_align"] === "bottom" ? " selected" : "")}
                        onClick={() => updateConfig({ ...config, h_align: "left", v_align: "bottom" })}>
                        <img className="icon" src="/Icons/Layout/bottom-left-alignment.png" alt="Icon text layout bottom left" />
                    </button>
                    <button
                        className={"select-button" + (config["h_align"] === "center" && config["v_align"] === "bottom" ? " selected" : "")}
                        onClick={() => updateConfig({ ...config, h_align: "center", v_align: "bottom" })}>
                        <img className="icon" src="/Icons/Layout/bottom-center-alignment.png" alt="Icon text layout bottom center" />
                    </button>
                    <button
                        className={"select-button" + (config["h_align"] === "right" && config["v_align"] === "bottom" ? " selected" : "")}
                        onClick={() => updateConfig({ ...config, h_align: "right", v_align: "bottom" })}>
                        <img className="icon" src="/Icons/Layout/bottom-right-alignment.png" alt="Icon text layout bottom right" />
                    </button>
                </div>
            </div>

            <div className="config">
                <label>Font</label>
                <select className="input" name="Font" id="font" value={config["font"]} onChange={v => updateConfig({ ...config, font: v.target.value })} >
                    {
                        FONTS.map((font) => <option key={font} value={font}>{font}</option>)
                    }
                </select>
            </div>

            <div className="config">
                <label>Schriftgre</label>
                <div>
                    <input className="input" type="range" min="20" max="800" value={config["font_size"]} onChange={v => updateConfig({ ...config, font_size: v.target.value })} id="font-size" />
                </div>
            </div>

            <div className="config">
                <label>Schriftfarbe</label>
                <div className="input">
                    <HexColorPicker className="color-picker" color={config["color"]} onChange={(color) => updateConfig({ ...config, color: color })} />
                </div>
            </div>
        </div>
    );
}


function savePersonalizedImage(image_id, url){
    fetch("/api/images/" + image_id + "/save", {
        method: "POST",
        body: url
    }).then(res => {
        if (res.ok) {
            window.alert("Ihr Bild wurde erfolgreich fr das Brautpaar gespeichert! :-)")
        }
        else {
            window.alert("Ein Fehler ist aufgetreten.\nBitte versuchen Sie es spter erneut.\nSollte es weiterhin nicht funktionieren, kontaktieren Sie bitte Christian.")
        }
    });
    /*fetch("/api/images/" + image_id + "/save_raw", {
        method: "POST",
        body: url
    });*/
}
