import { useEffect, useState } from "react";
import "./styling/Admin.css";


/**
 * - PRESENTING:
 * Current paper stock (GET, POST)
 * Print count
 * Problems
 * 
 * @returns 
 */
export default function Admin() {
    return (
        <div id="admin">
            <h2 style={{ marginLeft: "6vw" }}>ADMIN</h2>
            <PauseButton />
            <LastOrder />
            <Stock ressource="paper" maxStock={18}/>
            <Stock ressource="ink" maxStock={54}/>
            <PrintCount />
            <PrinterResponses />
        </div>
    );
}


function LastOrder() {
    const [lastOrder, setLastOrder] = useState(undefined);

    const updateLastOrder = () => {
        fetch("/api/printer/order")
            .then(async res => {
                if(res.status === 200) {
                    res.json()
                        .then(json => {
                            setLastOrder(json.timestamp);
                        })
                        .catch(error => {
                            console.error(error);
                        });
                }
                else {
                    setLastOrder("Error");
                }
            })
            .catch(error => {
                console.error(error);
            });
    }

    useEffect(() => {
        const interruptWatcher = new Boolean(false);
        watcher(updateLastOrder, 4000, interruptWatcher);

        return () => {
            interruptWatcher.valueOf = function(){ return true };
        }
    }, [])

    return (
        <div style={{ margin: "3vh 2vw" }}>
            <h3>{"Last Order: "}</h3>
            <p>{"Timestamp: " + lastOrder}</p>
        </div>
    );
}


function PauseButton() {
    const [isPaused, setPaused] = useState(false);

    useEffect(() => {
        fetch("/api/printer/is_paused")
            .then(res => {
                res.json()
                    .then(body => {
                        setPaused(body.is_paused);
                    })
                    .catch(error => {
                        console.error(error);
                    });
            })
            .catch(error => {
                console.error(error);
            });
    }, [])

    const pause = function (isPaused) {
        fetch("/api/printer/is_paused", {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({ is_paused: isPaused })
        })
            .then(async res => {
                if(res.status === 200) {
                    setPaused(isPaused);
                }
                else {
                    window.alert("Something went wrong while setting is_paused.\nPlease check printer backend for further information.");
                }
            });
    }

    return (
        <div style={{ margin: "3vh 2vw" }}>
            <button onClick={() => pause(!isPaused)}>{isPaused ? "Continue" : "Pause"}</button>
        </div>
    );
}


function Stock(props) {
    const [stock, setStock] = useState(Number.Nan);

    const updateStock = () => {
        fetch("/api/printer/" + props.ressource + "/stock")
            .then(async res => {
                if(res.status === 200) {
                    res.json()
                        .then(json => {
                            setStock(json.stock);
                        })
                        .catch(error => {
                            console.error(error);
                        });
                }
                else {
                    setStock(Number.NaN);
                }
            })
            .catch(error => {
                console.error(error);
            });
    }

    useEffect(() => {
        const interruptWatcher = new Boolean(false);
        watcher(updateStock, 4000, interruptWatcher);

        return () => {
            interruptWatcher.valueOf = function(){ return true };
        }
    }, [])

    const refill = () => {
        fetch("/api/printer/" + props.ressource + "/stock", {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({ stock: props.maxStock })
        })
            .then(async res => {
                if(res.status === 200) {
                    setStock(props.maxStock);
                }
                else {
                    window.alert("Something went wrong while setting stock to 18.\nPlease check printer backend for further information.");
                }
            });
    }

    return (
        <div style={{ margin: "3vh 2vw" }}>
            <h3>{"Current " + props.ressource + " stock: "}</h3>
            <p className={"stock" + (stock > 2 ? (stock > 5 ? "" : " warn") : (stock > 0 ? " alert" : " crisis"))}>{stock}</p>
            <button onClick={refill}>Refill</button>
        </div>
    );
}


function PrintCount() {
    const [count, setCount] = useState(undefined);

    const updateCount = () => {
        fetch("/api/printer/print_count")
            .then(async res => {
                res.json()
                    .then(json => {
                        setCount(json.count);
                    })
                    .catch(error => {
                        console.error(error);
                    });
            })
            .catch(error => {
                console.error(error);
            });
    }

    useEffect(() => {
        const interruptWatcher = new Boolean(false);
        watcher(updateCount, 10000, interruptWatcher);

        return () => {
            interruptWatcher.valueOf = function(){ return true };
        }
    }, [])

    return (
        <div style={{ margin: "3vh 2vw" }}>
            <h3>Print Count:</h3>
            <p>{count}</p>
        </div>
    );
}


function PrinterResponses() {
    const [responses, setResponses] = useState(undefined);

    console.log(responses);

    const updateResponses = () => {
        fetch("/api/admin/printer_responses")
            .then(async res => {
                console.log(res);
                res.json()
                    .then(json => {
                        console.log(json);
                        setResponses(json);
                    })
                    .catch(error => {
                        console.error(error);
                    });
            })
            .catch(error => {
                console.error(error);
            });
    }

    useEffect(() => {
        const interruptWatcher = new Boolean(false);
        watcher(updateResponses, 10000, interruptWatcher);

        return () => {
            interruptWatcher.valueOf = function(){ return true };
        }
    }, []);

    return (
        <div style={{ margin: "3vh 2vw" }}>
            <h3>Printer Responses:</h3>
            {
                responses !== undefined &&
                Object.keys(responses).map(key => 
                    <>
                        <p style={{ paddingLeft: "2vw" }}>{key}</p>
                        {
                            Object.keys(responses[key]).map(code => 
                                <p style={{ paddingLeft: "4vw" }}>{code + ": " + responses[key][code]}</p>
                            )
                        }
                    </>
                )
            }
        </div>
    );
}


async function watcher(action, interval, interrupter) {
    if(interrupter.valueOf()) {
        return;
    }

    action();
    setTimeout(() => watcher(action, interval, interrupter), interval);
}
