import { useEffect, useState } from "react";
import "./styling/LoginPage.css";
import { redirect, useNavigate } from "react-router";
import { useSearchParams } from "react-router-dom";


export default function LoginPage() {
    const [password, setPassword] = useState("");
    let navigate = useNavigate();
    const [searchParams, ] = useSearchParams();

    useEffect(() => {
        fetch("/api/login")
            .then(res => {
                res.json()
                    .then(json => {
                        
                        if(json.result) {
                            navigate("/");
                        }
                        else {
                            let password = searchParams.get("password");
                            if(password === null) {
                                return;
                            }
                            tryLogin(navigate, password)
                                .then(successful => {
                                    if(!successful) {
                                        window.alert("Anmelden ist schief gelaufen. Versuchen Sie es bitte erneut.\nWenn es weiterhin fehlschlgt, geben Sie das Passwort bitte manuell ein.");
                                    }
                                });
                        }
                    })
                    .catch(error => {
                        console.error(error);
                    });
            });
    });

    return(
        <div id="login-page">
            <h1>Passwort</h1>
            <input type="password" value={password} onChange={(value) => setPassword(value.target.value)}/>
            <button onClick={() => {
                tryLogin(navigate, password)
                    .then(successful => {
                        if(!successful) {
                            window.alert("Das eingegebene Passwort ist leider falsch.");
                        }
                    })
            }}>Eintreten</button>
        </div>
    );
}


function tryLogin(navigate, password) {
    return fetch("/api/login", { method: "POST", body: password })
        .then(response => {
            if(response.status === 401) {
                return false;
            }
            else {
                navigate("/");
                return true;
            }
        });
}