import { useLocation } from "react-router";
import { useSearchParams } from "react-router-dom";


export function HelpButton() {
    let location = useLocation();
    
    return (
        <a href={"/help?origin=" + location.pathname} className="help-button">
            <button className="button">Hilfe</button>
        </a>
    );
}


export function BackButton(props) {
    const [searchParams, ] = useSearchParams();
    let target = props.target !== undefined ? props.target : searchParams.get("origin");
    if(target === undefined) target = "/";
    let location = useLocation().pathname;    

    return (
        <a href={target + "?origin=" + location} className="back-button">
            <button className="button">Zurck</button>
        </a>
    );
}


export function Heading(props) {
    return (
        <h2>
            {props.title}
        </h2>
    );
}