import './styling/MainPage.css'

import { useSearchParams } from "react-router-dom";
import { useEffect, useRef } from 'react';


function MainPage(props){
    document.title = "Fotobox";

    return(
        <>
            <ImagesArea images={props.images} />
        </>
    );
}

export default MainPage;


function ImagesArea(props){
    const imagesContainer = useRef(null);

    const [searchParams, ] = useSearchParams();

    useEffect(() => {
        if(imagesContainer.current) {
            let origin = searchParams.get("origin");
            if(origin === null) {  
                return;
            }
            let scrollToID = "";
            if(origin !== undefined && origin.slice(0, "/images".length) === "/images") {
                scrollToID = origin.slice("/images/".length, "/images/XXXXXX".length);

                const imageAreas = imagesContainer.current.querySelectorAll("a");
                imageAreas.forEach((imageArea) => {
                    let image = imageArea.querySelector("img");
                    image.addEventListener("load", () => {
                        if(image.attributes.id.value === scrollToID){
                            scrollToImage(image);
                        }
                    });
                })
            }
        }
    });

    return (
        <div id="images-area" ref={imagesContainer}>
            {
                props.images.map((image_id) =>  <Image id={image_id} key={image_id} />)
            }
        </div>
    );
}


function Image(props) {
    return (
        <a href={"/images/" + props.id + "?origin=/"} >
            <img src={"/api/images/" + props.id + "/thumbnail"} alt={"Bild #" + props.id} id={props.id} />
        </a>
    );
}


export function scrollToImage(image){
    image.scrollIntoView({block: "center", behavior: "smooth"});
}